"use client";
import React, {JSX} from "react";

export default function Home(): JSX.Element {
    return (
        <main className="flex flex-col h-screen w-full justify-center items-center gap-16">
            <div className="text-white">Contact</div>
            <div className="justify-center">
                <section className="w-full">
                    <div className="max-w-[556px] px-4 py-10 mx-auto flex flex-col">
                        <div className="w-full flex flex-col gap-5">
                            {/* Radio buttons */}
                            <div className="flex gap-9">
                                <label className="flex items-center gap-4">
                                    <input
                                        type="radio"
                                        name="contactReason"
                                        className="w-7 h-7 accent-accent"
                                    />
                                    <span className="dark:text-white text-white text-base font-normal">
                                        Laisser un message
                                    </span>
                                </label>

                                <label className="flex items-center gap-4">
                                    <input
                                        type="radio"
                                        name="contactReason"
                                        className="w-7 h-7 accent-accent"
                                    />
                                    <span className="dark:text-white text-white text-base font-normal">
                                        Demande de devis
                                    </span>
                                </label>
                            </div>

                            {/* Form fields */}
                            <div className="w-full flex flex-col justify-start items-start gap-6">
                                {/* Name field */}
                                <div className="w-full">
                                    <label htmlFor="name" className="dark:text-white text-white text-base font-normal leading-7">
                                        Pr√©nom:
                                    </label>
                                    <input
                                        id="name"
                                        name="name"
                                        type="text"
                                        placeholder="Name"
                                        className="mt-2 w-full px-5 py-2 bg-white rounded-xl outline outline-1 outline-offset-[-1px] outline-black text-[#898989] text-lg font-normal"
                                    />
                                </div>

                                {/* Email field */}
                                <div className="w-full">
                                    <label htmlFor="email" className="dark:text-white text-white text-base font-normal leading-7">
                                        Email:
                                    </label>
                                    <input
                                        id="email"
                                        name="email"
                                        type="email"
                                        placeholder="Email"
                                        className="mt-2 w-full px-5 py-2 bg-white rounded-xl outline outline-1 outline-offset-[-1px] outline-black text-[#898989] text-lg font-normal"
                                    />
                                </div>

                                {/* Message field */}
                                <div className="w-full">
                                    <label htmlFor="message" className="dark:text-white text-white text-base font-normal leading-7">
                                        Message:
                                    </label>
                                    <textarea
                                        id="message"
                                        name="message"
                                        placeholder="Message"
                                        cols={40}
                                        rows={10}
                                        className="mt-2 w-full resize-none px-5 py-2 bg-white rounded-xl outline outline-1 outline-offset-[-1px] outline-black text-[#898989] text-lg font-normal"
                                    ></textarea>
                                </div>
                            </div>

                            {/* Submit button */}
                            <button
                                type="submit"
                                className="px-9 py-5 rounded-2xl bg-primary text-center text-white text-xl font-normal leading-7"
                            >
                                Send Message
                            </button>
                        </div>
                    </div>
                </section>
            </div>
        </main>
    );
}
